// router/index.js
import { createRouter, createWebHashHistory } from 'vue-router';
import Home from '../components/Home.vue'
import HotSearch from '../components/HotSearch.vue'
import Weather from '../components/Weather.vue'

const routes = [
  { path: '/', redirect: '/home'}, 
  { path: '/home', component: Home ,props: true}, 
  { path: '/hotsearch', component: HotSearch ,props: true}, 
  { path: '/weather', component:  Weather ,props: true}, 
  
];

const router = createRouter({
  history: createWebHashHistory(), // ‰ΩøÁî® HTML5 Ê®°ÂºèÔºàÈúÄÊúçÂä°Âô®ÊîØÊåÅÔºâ
  routes, // Ë∑ØÁî±ÈÖçÁΩÆ   
});

export default router; // ÂØºÂá∫Ë∑ØÁî±ÂÆû‰æã